.fb_post_grid_container{ 
	.fb_post_grid_wrapper{
		width: 100%;
		overflow: hidden;
		box-sizing: border-box;
		img{
			width: 100%;
			box-sizing: border-box;
			display: block;
			object-fit: cover;
		}
	}
	.fb_post_grid_categories {
		display: flex;
		align-items: center;
		gap: 10px;

		a {
			&:focus {
				outline: none;
				box-shadow: none;
			}
		}
	}
	.fb_post_grid_tags {
		display: flex;
		align-items: center;
		gap: 10px;
		a {
			&:focus {
				outline: none;
				box-shadow: none;
			}
		}
	}
	.fb_post_grid_content {
		.fb_post_title {
			&:focus {
				outline: none;
				box-shadow: none;
			}
		}
		a {
			text-decoration: none;
			&:focus {
				outline: none;
				box-shadow: none;
			}
		}
	}
}



.fb_post_grid_author {
	display: flex;
    align-items: center;

	a {
		&:focus {
			outline: none;
			box-shadow: none;
		}
	}

    .fb_post_grid_author_avatar {
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            display: block;
        }
    }

    .fb_post_grid_author_info {
		display: flex;
		align-items: center;
		.fb_post_grid_author_info_name {
			display: flex;
			align-items: center;
		}
    }
}

