.load_more_button_icon {
	display: none;
	visibility: hidden;
	opacity: 0;
}

.fb_post_grid_container{
	.fb_load_more_wrapper{
		overflow: hidden;
		.fb_load_more{
			cursor: pointer;
			display: inline-flex;
			align-items: center;
		
			span {
				width: 20px;
				height: 20px;
				position: relative;
				fill: #000000;
				transition: all 0.3s ease-in-out;

				svg {
					width: 100%;
					height: 100%;

				}
			}
		}
		.fb_load_more_loader {
			border-top: 2px solid #000000;
			border-right: 2px solid #00000099;
			border-bottom: 2px solid #0000004d;
			border-left: 2px solid #0000001a;
			border-radius: 50%;
			display: inline-block;
			box-sizing: border-box;
			animation: loader 1s linear infinite;
		}
		.load_more_button_icon_wrap {
			.load_more_button_icon {
				visibility: visible !important;
				opacity: 1 !important;
				display: flex !important;
				align-items: center;
				justify-content: center;
				svg {
					width: 20px;
					height: 20px;
				}
			}
		}
	}

	.fb_load_more_outter_wrap {
		display: none;
		position: absolute;
		top: 50%;
		transform: translateY(-40%);
		margin-left: 5px;
	}
	.empty-post{
		flex-basis: 100%;
	}
}


.skeleton-item {
	position: relative;
	display: flex;
	flex-direction: column;

	.skeleton-item-title {
		width: 100%;
		height: 260px;
	}

	.loading {
		position: relative;
	}

	.skeleton-item-location {
		width: 100%;
		height: 43px;
		margin: 32px 0 16px;
	}
	.skeleton-item-sport {
		width: 80%;
		height: 24px;
	}
}

.post-card.skeleton .loading:before {
	content: "";
	opacity: 0.7;
	-webkit-animation-duration: 2s;
	animation-duration: 2s;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
	-webkit-animation-name: loading;
	animation-name: loading;
	-webkit-animation-timing-function: linear;
	animation-timing-function: linear;
	background: -webkit-gradient(linear, left top, right top, color-stop(8%, #212121), color-stop(30%, #2b2b2b), color-stop(51%, #212121));
	background: -o-linear-gradient(to right, #212121 8%, #2b2b2b 30%, #212121 51%);
	background: linear-gradient(to right, #212121 8%, #2b2b2b 30%, #212121 51%);
	background-size: 800px 104px;
	position: absolute;
	left: 0;
	height: 100%;
	width: 100%;
}

.post-card.skeleton .post-card-content {
	width: 100%;
}

.post-card.skeleton .post-card-content .post-card-excerpt {
	width: 100%;
	margin-top: 2rem;
}

.post-card.skeleton .post-card-content .post-card-excerpt h5 {
	min-height: 1.75rem;
	width: 100%;
}

.post-card.skeleton .post-card-content .post-card-excerpt p {
	margin-top: 1rem;
	min-height: 3.5rem;
	width: 100%;
}

.post-card.skeleton .post-card-content .post-card-excerpt div {
	width: 50%;
	min-height: 3.25rem;
}


@-webkit-keyframes loading {
	0% {
		background-position: -800px 0;
	}
	100% {
		background-position: 800px 0;
	}
}

@keyframes loading {
	0% {
		background-position: -800px 0;
	}
	100% {
		background-position: 800px 0;
	}
}

@keyframes loader {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
} 





.fb_post_grid_filter_categories {
	display: flex;
	flex-wrap: wrap;
	gap: 16px;
	justify-content: center;
	margin-bottom: 60px;

	@media only screen and (max-width: 767px) {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 16px 8px;
		margin-bottom: 32px;
	}

	.fb_post_grid_filter_category  {
		cursor: pointer;
		color: #000000;
		font-family: Cabin;
		font-size: 16px;
		font-weight: 700;
		line-height: 20px;
		text-transform: uppercase;
		text-align: center;
		white-space: nowrap;
		padding: 16px 30px;
		border-radius: 50px;
		border: 1px solid #D9D9D9;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all .3s ease;
		&:hover {
			border-color: #A4000F;
			color: #A4000F;
			transition: all .3s ease-in-out;

			.fb-post-grid-filter-category-count {
				color: #000000;
				background-color: #F8A948;
				transition: all .3s ease-in-out;
			}
		}
		&.active {
			border-color: #A4000F;
			color: #A4000F;
			transition: all .3s ease-in-out;

			.fb-post-grid-filter-category-count {
				color: #000000;
				background-color: #F8A948;
				transition: all .3s ease-in-out;
			}
		}

		@media only screen and (max-width: 767px) {
			font-size: 12px;
			line-height: 14px;
			padding: 16px 0;
			width: 100%;
		}

		.fb-post-grid-filter-category-count {
			margin-left: 8px;
			width: 34px;
			background-color: #F0F0F0;
			border-radius: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			font-family: Cabin;
			font-size: 14px;
			font-weight: 700;
			line-height: 20px;
			transition: all .3s ease-in-out;

			@media only screen and (max-width: 767px) {
				font-size: 12px;
				line-height: 14px;
				width: 31px;
			}
		}
	}
}


.fb_pagination_previous_icon,
.fb_pagination_next_icon {
	display: none;
	visibility: hidden;
	opacity: 0;
}

.fb_pagination_wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 24px;
	border-top: 2px solid #E6E6E6;
	padding-top: 44px;
	margin-top: 12px;

	.fb_pagination {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 4px;
	}

	.fb_pagination_ellipsis {
		padding: 6px 10px;
		color: #999;
	}

	.fb_pagination_item {
		width: 36px;
    	aspect-ratio: 1 / 1;
		border: none;
		color: #000000;
		background-color: transparent;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;
		font-family: Cabin;
		font-size: 14px;
		font-weight: 400;
		border-radius: 50%;
		padding: 7px 15px;
		transition: all .3s ease-in-out;

		&:focus {
			outline: none;
			box-shadow: none;
		}
		&:hover {
			background-color: #F8A948;
			transition: all .3s ease-in-out;
		}
		&.active {
			background-color: #F8A948;
			transition: all .3s ease-in-out;
		}
	}

	.fb_pagination_previous_icon,
	.fb_pagination_next_icon {
		visibility: visible;
		opacity: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 12px;
		height: 12px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
}

.fb_pagination_previous,
.fb_pagination_next {
	width: 36px;
    aspect-ratio: 1 / 1;
	border: 1px solid #D9D9D9;
	color: #000000;
	background-color: transparent;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	padding: 7px 15px;
	transition: all .3s ease-in-out;

	&:hover {
		background-color: #F8A948;
		transition: all .3s ease-in-out;
	}

	&:focus {
		outline: none;
		box-shadow: none;
	}
	&.disabled{
		opacity:.4; 
		pointer-events:none; 
		cursor:default;
	}
}


.fb_post_grid_read_more_icon {
	display: none;
	visibility: hidden;
	opacity: 0;
	svg {
		width: 20px;
		height: 20px;
	}
}
.fb_post_grid_read_more_button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	text-decoration: none;
	box-sizing: border-box;
	&:focus {
		outline: none;
		box-shadow: none;
	}

	.fb_post_grid_read_more_icon_wrapper {
		line-height: 0;
		.fb_post_grid_read_more_icon {
			display: flex !important;
			visibility: visible !important;
			opacity: 1 !important;
		}
	}
	.fb_post_grid_read_more_icon_image {
		height: auto;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

